import PostsButtonGroup from "../posts/PostsButtonGroup";

import useMedia from "../../hooks/useMedia";
import { arrayToExcel } from "../../hooks/ArrayToExcel";

import Alert from "react-bootstrap/Alert";
import ButtonGroup from "react-bootstrap/ButtonGroup";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import Modal from "react-bootstrap/Modal";

import "./Spreadsheet.css";

import {
  // selectSpreadsheetValue,
  selectSpreadsheetProtoData,
  selectSpreadsheetData,
  selectSpreadsheetFormulaValue,
  selectSpreadsheetFormulaRowIndex,
  selectSpreadsheetFormulaColumnIndex,
  add_row_under,
  add_row_before,
  delete_row,
  add_column_after,
  add_column_before,
  delete_column,
  update_formula,
  update_data,
  selectSpreadsheetTitle,
  new_empty_spreadsheet,
  load_data,
  createProtoArray,
  createProtoObject,
} from "./spreadsheetSlice";

import createNewDraft from "./createNewDraft";

import { selectApplication } from "../application/applicationSlice";

export default function CompactSpreadsheetLayout() {
  const userProfile = useSelector(selectApplication);

  return (
    <div className="excelstyle">
      <div
        className="title"
        style={{
          display: "flex",
          justifyContent: "space-between",
          padding: ".4rem",
        }}
      >
        {userProfile?.avatarUrl && userProfile.avatarUrl.length > 10 ? (
          <img
            src={userProfile.avatarUrl}
            alt=""
            style={{
              verticalAlign: "middle",
              width: "30px",
              height: "30px",
              borderRadius: "50%",
              filter: "grayscale(100%)",
              objectFit: "cover",
            }}
          />
        ) : null}
        <LoginLogout />
      </div>

      <ActiveCellsMediumSize userProfile={userProfile} />
    </div>
  );
}

function ActiveCellsMediumSize() {
  const data = useSelector(selectSpreadsheetData);
  const dispatch = useDispatch();
  const { register, handleSubmit, reset, setValue } = useForm({
    defaultValues: createProtoObject(data),
  });
  const [result, setResult] = useState(null);
  const [showColumns, setShowColumns] = useState(null);
  const [calculatedCells, setCalculatedCells] = useState({});



  let numberOfX = showColumns - 1; //cellsArray[0].length - 1;
  let numberOfY = 8;

  const onSubmit = (submitData) => {
    dispatch(load_data({ protoData: createProtoArray(submitData, 0, 0) }));
 //   console.log(submitData);

    let updatedCalculatedCells = {};
    Object.keys(submitData).map(key => { if (submitData[key].includes('=')) { updatedCalculatedCells[key] = submitData[key] } })
 //   console.log(updatedCalculatedCells);
    setCalculatedCells(updatedCalculatedCells)
  };

  function recalculate() {
    let spreadsheetObject = createProtoObject(data);
    let updatedResult = {};
    Object.keys(spreadsheetObject).map((key) => {
      if (!(typeof spreadsheetObject[key] === "string" || spreadsheetObject[key] instanceof String)) { updatedResult[key] = spreadsheetObject[key]; }
    });
    setResult(updatedResult);
  }

  // console.log(calculatedCells);
  // console.log(result);


  return (
    <>
      <form onSubmit={handleSubmit(onSubmit)}>
        <div className="icon-bar">
          <ButtonGroup aria-label="Posts Buttons" size="sm">
            <Button
              variant="outline-secondary"
              onClick={() => {
                reset({ A1: "", A2: "", A3: "", A4: "", A5: "", A6: "", A7: "", A8: "" });
                dispatch(
                  load_data({
                    protoData: [[""], [""], [""], [""], [""], [""], [""], [""]],
                  })
                );
                setShowColumns(1);
              }}
              data-toggle="tooltip"
              data-placement="bottom"
              title="Новый (одна колонка)"
            >
              Нов |
            </Button>

            <Button
              variant="outline-secondary"
              onClick={() => {
                reset({
                  A1: "",
                  A2: "",
                  A3: "",
                  A4: "",
                  A5: "",
                  A6: "",
                  A7: "",
                  A8: "",
                  B1: "",
                  B2: "",
                  B3: "",
                  B4: "",
                  B5: "",
                  B6: "",
                  B7: "",
                  B8: "",
                });
                dispatch(
                  load_data({
                    protoData: [
                      ["", ""],
                      ["", ""],
                      ["", ""],
                      ["", ""],
                      ["", ""],
                      ["", ""],
                      ["", ""],
                      ["", ""],
                    ],
                  })
                );
                setShowColumns(2);
              }}
              data-toggle="tooltip"
              data-placement="bottom"
              title="Новый (две колонки)"
            >
              Нов ||
            </Button>

            <Button
              variant="outline-secondary"
              onClick={() => {
                reset({
                  A1: "",
                  A2: "",
                  A3: "",
                  A4: "",
                  A5: "",
                  A6: "",
                  A7: "",
                  A8: "",
                  B1: "",
                  B2: "",
                  B3: "",
                  B4: "",
                  B5: "",
                  B6: "",
                  B7: "",
                  B8: "",
                  C1: "",
                  C2: "",
                  C3: "",
                  C4: "",
                  C5: "",
                  C6: "",
                  C7: "",
                  C8: "",
                });
                dispatch(
                  load_data({
                    protoData: [
                      ["", "", ""],
                      ["", "", ""],
                      ["", "", ""],
                      ["", "", ""],
                      ["", "", ""],
                      ["", "", ""],
                      ["", "", ""],
                      ["", "", ""],
                    ],
                  })
                );
                setShowColumns(3);
              }}
              data-toggle="tooltip"
              data-placement="bottom"
              title="Новый (три колонки)"
            >
              Нов |||
            </Button>

            <Button
              variant="outline-secondary"
              onClick={() => { reset(createProtoObject(data)) }}
              data-toggle="tooltip"
              data-placement="bottom"
              title="Показывать значения"
            >
              123
            </Button>

            <Button
              variant="outline-secondary"
              onClick={() => { reset({ ...createProtoObject(data), ...calculatedCells }) }}
              data-toggle="tooltip"
              data-placement="bottom"
              title="Показывать функции"
            >
              Fx
            </Button>

          </ButtonGroup>
        </div>

        {!!showColumns && (
          <div>
            <div
              className="cells"
              style={{
                gridTemplateColumns: `40px repeat(${numberOfX + 1
                  }, calc((100% - 50px) / ${numberOfX + 1}))`,
                gridTemplateRows: `repeat(${numberOfY}, 25px)`,
              }}
            >
              <div className="cells__spacer"></div>
              <AlphabetRow x={numberOfX} />
              <NumbersColumns y={numberOfY} />

              {data.map((row, rowIndex) => {
                return row.map((column, columnIndex) => {
                  return (
                    <input
                      key={alphabet[columnIndex] + (rowIndex + 1)}
                      name={alphabet[columnIndex] + (rowIndex + 1)}
                      className={"cells__input"}
                      //    defaultValue={data[rowIndex][columnIndex]}
                      ref={register}
                    />
                  );
                });
              })}
            </div>
            <Button onClick={() => { handleSubmit(); recalculate() }}
              variant="outline-secondary" size="sm" block
              type="submit" className="mt-2"
            >
              <span>Рассчитать fx &#10003;</span>
            </Button>
          </div>
        )}
        {!!result ? (
          <Alert variant={"success"} className="mt-2">
            Результат: {Object.keys(result).map(key => <div key={key}>{key}: {result[key]}</div>)}
          </Alert>
        ) : (
            <Alert variant={"success"} className="mt-2">
              Калькулятор с поддержкой XL функций
            </Alert>
          )}
      </form>
    </>
  );
}
