import React from "react";

import useMedia from "../hooks/useMedia";

import Alert from "react-bootstrap/Alert";

const ApexEmptyOptionsChartLazy = React.lazy(() => import("./lazy/ApexEmptyOptionsChartLazy.js"))

export default (props) => {
  const screenSize = useMedia(
    // Media queries
    ['(min-width: 500px)', '(min-width: 100px)'],
    // Column counts (relates to above media queries by array index)
    ['large', 'small'],
    // Default column count
    'large'
  );

  if (screenSize === 'small') { return   <Alert variant={'success'}>График (доступен только для широких экранов)</Alert>}
    
  const isSSR = typeof window === "undefined"
    return <>
      {!isSSR && (
        <React.Suspense fallback={<div />}><ApexEmptyOptionsChartLazy {...props} /></React.Suspense>
      )}
    </>
}
  
  
