import React from 'react'
import MathJax from 'react-mathjax-preview'

function ShowMathJax(props) {

  if (typeof window === `undefined`) return <div>...</div>
  return <div><MathJax math={String.raw`<math xmlns="http://www.w3.org/1998/Math/MathML" display="block">${props.math}</math>`} /></div>
}

export default ShowMathJax


