import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import firebase from "gatsby-plugin-firebase"

import Card from "react-bootstrap/Card";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import InputGroup from "react-bootstrap/InputGroup";
import FormControl from "react-bootstrap/FormControl";
import Form from "react-bootstrap/Form";

import { Parser as FormulaParser } from "hot-formula-parser";
import ReactHtmlParser from 'react-html-parser'; //, { processNodes, convertNodeToElement, htmlparser2 }

import { createPost } from "../features/posts/postsSlice";
import {
  selectSpreadsheetProtoData, createProtoObject
} from "../features/spreadsheet/spreadsheetSlice";
import {
  selectApplication,
} from "../features/application/applicationSlice";

import SpreadsheetLayout from "../features/spreadsheet/SpreadsheetLayout";

function shuffle(array) {
  return array.sort(() => Math.random() - 0.5);
}

function QuizCardWithStorage(props) {
  const [show, setShow] = useState(false);

  let randomNumber = Math.random() * 10;

  if (props.text.includes("{=")) {
    return <QuizWithRandomNumber {...props} randomNumber={randomNumber} />;
  }

  if (!!props?.choices) {
    let { choices, ...other } = props;
    return <MultipleChoicesQuiz choices={shuffle(choices)} {...other} />;
  }


  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      <Card bg={"light"} style={{ width: "95%", margin: "1rem" }}>
        <Card.Header onClick={handleShow}>{props.header}</Card.Header>
        <Card.Body>
        {!!props.imageurl ? <Card.Img variant="top" src={props.imageurl} />: null} 
          <Card.Title>{props.title}</Card.Title>

          <Card.Text>
            {ReactHtmlParser(props.text)}
            {/* <ReactMarkdown source={props.text} escapeHtml={false} /> */}
          </Card.Text>
          {props.children}

        </Card.Body>
      </Card>
      <Modal show={show} onHide={handleClose} size="xl" scrollable centered>
        <Modal.Header closeButton>
          <Modal.Title>{props.title}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div>{ReactHtmlParser(props.text)}</div>
          {/* <ReactMarkdown source={props.text} escapeHtml={false} /> */}
          {props.children}
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Закрыть
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

export default QuizCardWithStorage;



function MultipleChoicesQuiz(props) {
  const [show, setShow] = useState(false);
  const [showAnswer, setShowAnswer] = useState(null);
  const [answerIsRight, setAnswerIsRight] = useState(null);
  const [value, setValue] = useState("");

  const dispatch = useDispatch();
 // const content = useSelector(selectSpreadsheetProtoData);
  const email = useSelector(selectApplication).email;
  const user = useSelector(selectApplication).user;
  const avatarUrl = useSelector(selectApplication)?.avatarUrl;


  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  function handleCheckboxChange(event) {
    const target = event.target
    const checked = target.checked
    const name = target.name
    setValue(name);
  }

  function handleCheckAnswer() {
    setShowAnswer(true);
    if (value === props.answers[0]) {
      setAnswerIsRight(true);

      if (email.length > 6) {
        let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");
        let idPost = firebase.database().ref(userEmail).child('posts').push().key;
        let currentDay = new Intl.DateTimeFormat("en", {
          weekday: "short",
          year: "numeric",
          month: "short",
          day: "numeric"
        }).format(new Date()).replace(/[^a-zA-Z0-9]/g, "_");

        let postObject = {
          id: idPost, //Math.floor(Math.random() * 1001),
          title: props.title,
          theme: props.theme,
          answer: props.answers[0],
          comment: props.title + " (" + props.theme + ")", //Тема
          type: "multiplechoices",
          content: props.text,
          quizString: props.text,
          deleted: false,
          email: email,
          user: user,
          avatarUrl: !!avatarUrl ? avatarUrl : '',
          date: new Intl.DateTimeFormat("ru", {
            weekday: "short",
            year: "numeric",
            month: "short",
            day: "numeric",
            hour: "numeric",
            minute: "numeric",
          }).format(new Date()), //Date().toJSON()
        };

        let currentDayObject = {
          id: idPost,
          title: props.title,
          theme: props.theme,
          email: email,
          user: user,
          avatarUrl: !!avatarUrl ? avatarUrl : null,
          timestamp: +Date.now()
        }

        dispatch(createPost(postObject));
        var updates = {};
        updates['/usersCraft/' + userEmail + '/posts/' + idPost] = postObject;
        updates['/currentDay/' + currentDay + '/posts/' + idPost] = currentDayObject;
        // updates['/posts/' + user +  newPostKey] = postData;
        // updates['/user-posts/' + uid + '/' + newPostKey] = postData;
        return firebase.database().ref().update(updates);
      }
    }
  }

  return <>
    <Card bg={"light"} style={{ width: "95%", margin: "1rem" }}>
      <Card.Header onClick={handleShow}>{props.header}</Card.Header>
      <Card.Body>
        <Card.Title> {props.title}</Card.Title>
        {!!props.imageurl ? <Card.Img variant="top" src={props.imageurl} />: null} 
        <Card.Text>
          {ReactHtmlParser(props.text)}
        </Card.Text>
        <Card.Text>
          {/*       <ReactMarkdown source={props.text} escapeHtml={false} /> */}
          {props.choices.map((item, index) =>
            <Form.Group controlId={"formBasicCheckbox" + index} key={index}>
              <Form.Check type="checkbox" label={item} onChange={handleCheckboxChange} name={item} />
            </Form.Group>
          )}
        </Card.Text>
      </Card.Body>
      <InputGroup size="sm" style={{ width: "95%", margin: "1rem" }}>
        <InputGroup.Prepend>
          {showAnswer ? (
            <InputGroup.Text id="basic-addon1">
              <span
                className={answerIsRight ? "text-success" : "text-danger"}
              >
                Правильный ответ:  {props.answers[0]}
              </span>
            </InputGroup.Text>
          ) : (
              <Button variant="outline-secondary" onClick={handleCheckAnswer}>
                Ответ
              </Button>
            )}
        </InputGroup.Prepend>
      </InputGroup>
    </Card>
    <Modal show={show} onHide={handleClose} size="xl" scrollable centered>
      <Modal.Header closeButton>
        <Modal.Title>{props.title}</Modal.Title>
      </Modal.Header>
      <Card.Body>
        <Card.Title> {props.title}</Card.Title>
        <Card.Text>
          {ReactHtmlParser(props.text)}
        </Card.Text>
        <Card.Text>
          {/* <ReactMarkdown source={props.text} escapeHtml={false} /> */}
          {props.choices.map((item, index) =>
            <Form.Group controlId={"formBasicCheckbox" + index} key={index}>
              <Form.Check type="checkbox" label={item} onChange={handleCheckboxChange} name={item} />
            </Form.Group>
          )}
        </Card.Text>
      </Card.Body>
      <InputGroup size="sm" style={{ width: "95%", margin: "1rem" }}>
        <InputGroup.Prepend>
          {showAnswer ? (<div>
            <InputGroup.Text id="basic-addon1">
              <span
                className={answerIsRight ? "text-success" : "text-danger"}
              >
                Правильный ответ: {props.answers[0]}
              </span>
            </InputGroup.Text>
            {!!props?.hint ? <div className="text-secondary m-1">{ReactHtmlParser(props.hint)}</div>
              // <ReactMarkdown source={props.hint} escapeHtml={false} /></div> 
              : null}
          </div>

          ) : (
              <Button variant="outline-secondary" onClick={handleCheckAnswer}>
                Ответ
              </Button>
            )}
        </InputGroup.Prepend>
      </InputGroup>
      <Modal.Footer>
      </Modal.Footer>
    </Modal>
  </>
}

function QuizWithRandomNumber(props) {
  const [show, setShow] = useState(false);
  const [value, setValue] = useState("");
  const [showAnswer, setShowAnswer] = useState(null);
  const [answerIsRight, setAnswerIsRight] = useState(null);

  const dispatch = useDispatch();
  const content = useSelector(selectSpreadsheetProtoData);
  const email = useSelector(selectApplication).email;
  const user = useSelector(selectApplication).user;
  const avatarUrl = useSelector(selectApplication)?.avatarUrl;


  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  function handleChange(event) {
    setValue(event.target.value);
  }

  let parser = new FormulaParser();

  function extract([beg, end]) {
    const matcher = new RegExp(`${beg}(.*?)${end}`, "gm");
    const normalise = (str) => str.slice(beg.length, end.length * -1);
    return function (str) {
      return str.match(matcher).map(normalise);
    };
  }

  let quizString = props.text;
  //let quizString = `this {={var1-10}+1} some {=2+{var1-10}} that can be {=3+{var1-10}} with a {=4+{var1-10}} function`;
  const searchRegExp = /{var1-10}/g;
  const replaceWith = props.randomNumber.toString();
  quizString = quizString.replace(searchRegExp, replaceWith);

  let answer = props.answer.replace(searchRegExp, replaceWith);
  answer = Math.round(parser.parse(answer).result * 1000) / 1000;

  let stringExtractor = extract(["{=", "}"]);
  let stuffIneed = stringExtractor(quizString);
  //console.log(stuffIneed);
  // Outputs: [ 'is', 'text', 'extracted', 'reusable' ]

  for (let i = 0; i < stuffIneed.length; i++) {
    let feedback = Math.round(parser.parse(stuffIneed[i]).result * 1000) / 1000;
    // console.log(answer);
    quizString = quizString.replace("{=" + stuffIneed[i] + "}", feedback);
  }

  function handleCheckAnswer() {
    setShowAnswer(true);
    if (
      parseFloat(value) / parseFloat(answer) < 1.02 &&
      parseFloat(value) / parseFloat(answer) > 0.98
    ) {
      setAnswerIsRight(true);
      if (email.length > 6) {
        let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");
        let idPost = firebase.database().ref(userEmail).child('posts').push().key;
        let currentDay = new Intl.DateTimeFormat("en", {
          weekday: "short",
          year: "numeric",
          month: "short",
          day: "numeric"
        }).format(new Date()).replace(/[^a-zA-Z0-9]/g, "_");

        let postObject = {
          id: idPost, //Math.floor(Math.random() * 1001),
          title: props.title,
          theme: props.theme,
          answer: answer,
          comment: props.title + " (" + props.theme + ")", //Тема
          type: "spreadsheet",
          content: createProtoObject(content),
          quizString: quizString,
          deleted: false,
          email: email,
          user: user,
          avatarUrl: !!avatarUrl ? avatarUrl : null,
          date: new Intl.DateTimeFormat("ru", {
            weekday: "short",
            year: "numeric",
            month: "short",
            day: "numeric",
            hour: "numeric",
            minute: "numeric",
          }).format(new Date()), //Date().toJSON()
        };

        let currentDayObject = {
          id: idPost,
          title: props.title,
          theme: props.theme,
          email: email,
          user: user,
          avatarUrl: !!avatarUrl ? avatarUrl : null,
          timestamp: +Date.now()
        }

        dispatch(createPost(postObject));
        var updates = {};
        updates['/usersCraft/' + userEmail + '/posts/' + idPost] = postObject;
        updates['/currentDay/' + currentDay + '/posts/' + idPost] = currentDayObject;
        // updates['/posts/' + user +  newPostKey] = postData;
        // updates['/user-posts/' + uid + '/' + newPostKey] = postData;
        return firebase.database().ref().update(updates);
      }
    }
  }

  return (
    <>
      <Card bg={"light"} style={{ width: "95%", margin: "1rem" }}>
        <Card.Header onClick={handleShow}>{props.header}</Card.Header>
        <Card.Body>
          <Card.Title>{props.title}</Card.Title>
          <Card.Text>{ReactHtmlParser(quizString)}</Card.Text>
          {/* <Card.Text>
            <ReactMarkdown source={quizString} escapeHtml={false} />
          </Card.Text> */}
          <SpreadsheetLayout quizString={quizString} theme={props.theme}/>
        </Card.Body>
        <InputGroup size="sm" style={{ width: "95%", margin: "1rem" }}>
          <InputGroup.Prepend>
            {showAnswer ? (
              <div>
                <InputGroup.Text id="basic-addon1">
                  <span
                    className={answerIsRight ? "text-success" : "text-danger"}
                  >
                    Правильный ответ: {answer}
                  </span>
                </InputGroup.Text>
                {!!props?.hint ? <div className="text-secondary m-1">{ReactHtmlParser(props.hint)}</div>
                  // <ReactMarkdown source={props.hint} escapeHtml={false} /></div>
                  : null}
              </div>

            ) : (
                <Button variant="outline-secondary" onClick={handleCheckAnswer}>
                  Ответ
                </Button>
              )}
          </InputGroup.Prepend>
          <FormControl
            value={value}
            type="number"
            onChange={handleChange}
            aria-label="Answer"
            aria-describedby="inputGroup-answer"
          />
        </InputGroup>
      </Card>
      <Modal show={show} onHide={handleClose} size="xl" scrollable centered>
        <Modal.Header closeButton>
          <Modal.Title>{props.title}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div>{ReactHtmlParser(quizString)}</div>
          {/* <ReactMarkdown source={quizString} escapeHtml={false} /> */}
          <SpreadsheetLayout quizString={quizString} title={props.title} answer={answer} answerIsRight={answerIsRight} theme={props.theme}/>
        </Modal.Body>
        <Modal.Footer>
          <InputGroup size="sm" style={{ width: "95%", margin: "1rem" }}>
            <InputGroup.Prepend>
              {showAnswer ? (
                <InputGroup.Text id="basic-addon1">
                  <span
                    className={answerIsRight ? "text-success" : "text-danger"}
                  >
                    Правильный ответ: {answer}
                  </span>
                </InputGroup.Text>
              ) : (
                  <Button variant="outline-secondary" onClick={handleCheckAnswer}>
                    Ответ
                  </Button>
                )}
            </InputGroup.Prepend>
            <FormControl
              value={value}
              type="number"
              onChange={handleChange}
              aria-label="Answer"
              aria-describedby="inputGroup-answer"
            />
          </InputGroup>
        </Modal.Footer>
      </Modal>
    </>
  );
}
